.incomplete-text {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 20px;
    margin-top: 40px;
    text-align: center;

    @media screen and (max-width: 768px) {
        flex-direction: column;
        align-items: center;
        width: auto;
        line-height: 150%;
        
    }

}

.incomplete-link {
    text-decoration: none;
    color: rgb(158, 14, 151);
    font-weight: bold;
}